<template>
   <div>
   	<div class="row">
	  <div class="col-6">
	    <apexchart type="area" :options="block.chartOptions" :series="block.series"></apexchart>
	  </div>
	  <div class="col-6">
	  </div>
    </div>      
   </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
	    return {    
	    	block: {}
	    }
    },
    methods: {
    	fetchEventsList() {
	      	axios
	        	.get("http://127.0.0.1:8000/api/sensor/graph1")
	        	.then(response => (this.block = response.data));
	        	
	        console.log(this.block)


	    }
  	},
  	mounted () {
    	this.fetchEventsList()
    	this.timer = setInterval(this.fetchEventsList, 30000)

  	}
};
</script>